import {post,get} from './fetch.js'

const api = {
  topics: '/topics'
}

export default {
  actions: {
    LOGIN: ({state,commit}, user) => {
      return new Promise(function (resolve, reject) {
        post(api.login,user).then(function (u) {
          global.user.saveUser(u)
          resolve()
        }).catch(function (e) {
          reject(e)
        })
      })
    },
    topics : ({},{page = 1, limit = 10}) => {
      console.log('topics')
      get(api.topics, { page, limit }, false).then(function (data) {
        console.log(data)
      })
    }
  }
}
